import React, { Component } from "react";
import { Link } from "react-router-dom";
import { reduxForm, Field } from "redux-form";
import { compose } from "redux";
import { connect } from "react-redux";

import * as actions from "actions";

class Signup extends Component {
  componentDidMount() {
    if (this.props.auth) {
      this.props.history.push("/dashboard");
    }
  }
  onSubmit = (formValues) => {
    this.props.signup(formValues, () => {
      this.props.history.push("/signin");
    });
  };
  renderError({ error, touched }) {
    // destructed the input to method because we only need error and touched
    if (touched && error) {
      return (
        <div>
          <div>{error}</div>
        </div>
      );
    }
  }
  renderInput = ({ input, label, type, meta }) => {
    return (
      <div>
        <label>{label}</label>
        <input {...input} autoComplete="off" type={type} />
        {this.renderError(meta)}
      </div>
    );
  };
  render() {
    const { handleSubmit } = this.props;
    return (
      <section className="section-signin">
        <div className="signin">
          <h2 className="signin__heading">ExchangerPoint</h2>
          <nav>
            <ul className="signin__navigation">
              <li className="signin__item">
                <Link to="/signin" className="signin__link">
                  Login
                </Link>
              </li>
              <li className="signin__item">
                <Link to="#" className="signin__link signin__link--active">
                  Signup
                </Link>
              </li>
            </ul>
          </nav>
          <div className="signin__box">
            <form onSubmit={handleSubmit(this.onSubmit)} className="form">
              <Field
                name="userName"
                type="text"
                component={this.renderInput}
                label="Username:"
              />
              <Field
                name="email"
                type="text"
                component={this.renderInput}
                label="Email:"
              />
              <Field
                name="password"
                type="password"
                component={this.renderInput}
                label="Password:"
              />
              <button type="submit">Create account</button>
            </form>
          </div>
          <Link to="#" className="forgot-pasword">
            Forgot Your Password?
          </Link>
        </div>
      </section>
    );
  }
}
const validate = (formValues) => {
  const errors = {};
  if (!formValues.userName) {
    errors.email = "You must enter an User Name";
  }
  if (!formValues.email) {
    errors.email = "You must enter an Email";
  }
  if (!formValues.password) {
    errors.password = "You must enter a password";
  }

  return errors;
};

function mapStateToProps(state) {
  return {
    errorMessage: state.auth.errorMessage,
    auth: state.auth.authenticated,
  };
}

export default compose(
  connect(mapStateToProps, actions),
  reduxForm({ form: "signup", validate })
)(Signup);
